export async function generateStaticParams() {
  // Hanya build 200 untuk Vercel, sisanya on-demand
  return treatyData.slice(0, 200).map((treaty) => ({
    slug: `tax-treaty-${treaty.pair.toLowerCase().replace(/\s+/g, '-').replace(/[–—]/g, '-')}`,
  }))
}

